export default {
    
    getModalActionAndData: state =>  state,

    getModalTitle: state => {
        switch (state.heading) {
            case 'report_title':
                return 'समस्या नोंदवा';
            case 'edit_pratilipi_summary':
            	return 'रचनेचे सारांश';
            case 'edit_pratilipi_title':
            	return 'शिर्षक (मराठी)';
            case 'pratilipi_delete_content':
                return 'हटवा';
        }
    },

    getInputModalSubheadings: state => {
    	switch (state.heading) {
            case 'report_title':
                return ['समस्या नोंदवा'];
            case 'edit_pratilipi_summary':
            	return ['सारांश प्रविष्ट करा'];
            case 'edit_pratilipi_title':
            	return ['इथे शिर्षक लिहा', 'शिर्षकाला पुन्हा इंग्रजीत लिहा'];
            default:
            	return []
        }	
    },

    getModalMessage: state => {
        switch (state.message) {
            case 'pratilipi_confirm_delete_content':
                return 'आपण सामग्री हटवू इच्छिता का?';
        }
    }
}



